const btnOurWork = document.querySelector('.btn__seo');
const sectionOurWork = document.querySelector('.our-work');
const sidebar = document.querySelector('#wrapper');
const btnSideBarClose = document.querySelector('#menu-close');
const hamburger = document.querySelector('#menu-toggle');
const popup = document.querySelector('.popup');
const popupOpen = document.querySelector('#popup-open');
const popupClose = document.querySelector('#popup-close');
const overlay = document.querySelector('.overlay');

// Функция выводит плавно блок на экран
function showBlock(el, speed, btn = null) {
	el.style.display = 'block';
  let step = 1 / speed;
  let interval = setInterval(function() {
    if (+el.style.opacity >= 1)
      clearInterval(interval);
    if (btn) {
      btn.style.opacity = +btn.style.opacity - step; 
      if (+btn.style.opacity <= 0) 
        btn.style.display = "none";
    }
    el.style.opacity = +el.style.opacity + step;
    
  }, speed / 1000);
}

// Закрытие сайдбара
btnSideBarClose.addEventListener('click', () => {
  sidebar.classList.remove('toggled');
  btnSideBarClose.style.opacity = 0;
})

// Открытие блока "Наши работы"
btnOurWork.addEventListener('click', () => {
	showBlock(sectionOurWork, 300, btnOurWork)
})


// Открытие гамбургера
hamburger.addEventListener('click', (evt) => {
  evt.stopPropagation();
  sidebar.classList.toggle('toggled');
  btnSideBarClose.style.opacity = 1;
})

// Закрытие гамбургера при клике в любое другое место
document.addEventListener('click', (evt) => {
  let target = evt.target;
  let itsHamburger = target == hamburger;
  let itsMenu = target == sidebar || sidebar.contains(target);
  let menuIsToggled = sidebar.classList.contains('toggled');

  if (!itsMenu && !itsHamburger && menuIsToggled) {
    sidebar.classList.remove('toggled');
    btnSideBarClose.style.opacity = 0;
  }
})
// Открытие popup 
popupOpen.addEventListener('click', () => {
  showBlock(overlay, 150);
  showBlock(popup, 200);
})

//Закрытие popup
popupClose.addEventListener('click', () => {
  popup.style.display = 'none';
  popup.style.opacity = 0;
  overlay.style.display = 'none';
  overlay.style.opacity = 0;
})

